.checkbox {
  vertical-align: top;
  width: 25px;
  height: 25px;
  margin-bottom: 175px;
}

.checkbox.invalid + .label:before {
  border: 1px solid red;
}

.checkbox.invalid + .label + .label-for {
  color: red;
}

.checkbox.invalid + .label + .label-for a {
  color: red;
}

.checkbox + .label {
  cursor: pointer;
}

.checkbox:not(checked) {
  position: absolute;
  opacity: 0;
}

.checkbox:not(checked) + .label {
  position: relative;
  padding: 0 0 0 40px;
}

.checkbox:not(checked) + .label:before {
  content: '';
  position: absolute;
  top: -4px;
  left: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: transparent;
  border: 1px solid #2d6965;
}

.checkbox:not(checked) + .label:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 8px;
  width: 15px;
  height: 9px;
  border: 3px solid #ffffff;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
  transition: all .2s;
  opacity: 0;
  background: transparent !important;
}

.checkbox:checked + .label:before {
  background: #2d6965;
}

.checkbox:checked + .label:after {
  opacity: 1;
}

.checkbox:checked + .label + .label-for h3, .checkbox:checked + .label + .label-for p {
  color: #2d6965;
}

